import React from "react"
import "./Forecast.css"

export const Forecast = ({ forecast, fontSize }) => {
  const day = ["ND", "PN", "WT", "≈öR", "CZW", "PT", "SB"]
  const dayNum = new Date().getDay()
  let nextDay = []
  for (var x = dayNum; x < 13; x++) {
    nextDay.push(day[x % 7])
  }

  return (
    <div className="wrapper">
      <div style={{ fontSize: fontSize }}>
        <p>{nextDay[1]}</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{nextDay[2]}</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{nextDay[3]}</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{nextDay[4]}</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{nextDay[5]}</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{forecast[0]}Àö</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{forecast[1]}Àö</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{forecast[2]}Àö</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{forecast[3]}Àö</p>
      </div>
      <div style={{ fontSize: fontSize }}>
        <p>{forecast[4]}Àö</p>
      </div>
      <span className="break_2" />
    </div>
  )
}
